@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  font-family: 'Montserrat', sans-serif;
}

.back-to-top{
  position: fixed;
  top:90vh;
  margin-left:90vw;
  font-size: 17px;
  color: #0F4C81;
}
.back-to-top:hover{cursor: pointer;}

/*Header*/
.header{
  height: 132px;
  width: 100%;
}
.header-top{
  display: flex;
  flex-direction: row;
}
/*on scrolling*/
.scrolled{
  position: fixed;
  top: 0;
  z-index: 10;
}

.header-video{
  padding:0;
  margin: 0px 0 0 0;
}
.h-navbar {
  height:52.5px;
  list-style-type: none;
  margin: 0px 0 0 -1px;
  padding: 4px 0 0px 0;
  overflow: hidden;
  background-color: #203e91;
  width: 100%;
  box-shadow: 0 8px 6px -6px rgb(156, 156, 156);
}
.page1, .page2, .page3, .page4, .page5, .page6{
  float: right;
  text-align: center;
  text-decoration: none;
  padding: 3px 30px 0px 24px;
}

.header-text,.header-text-dd{
  font-size: 13px;
  font: 12px/1.5 'Open Sans', arial, sans-serif;
  letter-spacing: 0.15em;
  color: white;
}
.header-text:hover{
  color: #63676b;
}

li {
  float:left;
}
.li1, .li2, .li3, .li4,.li5,.li6{
  float: right;
}
/*dropdowns*/
.project-dd, .club-dd {
  display: none;
  position: fixed;
  background-color: #203e91;
  margin: 20px 0px 0px 0px;
  min-width: 130px;
  box-shadow: 0px 8px 16px 0px rgba(0,0,0,0.2);
  z-index: 11;
  overflow: visible;
}
.link-i{
  font-family: 'Montserrat',sans-serif;
  color: white;
  text-decoration: none;
  display: block;
  text-align: left;
  padding-top: 10px;
}

.li5, .li4 {
  display: inline-block;
}

.link-i, .page4, .page5 {
  display: inline-block;
  color: black;
  text-align: center;

  text-decoration: none;
  position: relative;
}
.link-i:hover {background-color: #4581b6;}

.li5:hover .project-dd,
.li4:hover .club-dd {
  display: flex;
  flex-direction: column;
  z-index: 99;
}

.page4, .page5{
  padding: 2px 15px;
}

.link-i p{
  color:white;
  font-family: 'Trebuchet MS', sans-serif;
  font-size: small;
}

/*Icons*/
.hp_icons{
