/*Image resolution: 1080 x 380 */
.slideshow {
    margin: auto;
    overflow: hidden;
    max-width: 100vw;
  }
  .slideshowSlider {
    
    white-space: nowrap;
    transition: ease 1000ms;
    position: relative;
    width: 100%;
    /*height: 400px;*/
  }
  .slide {
    display: inline-block;
    width: 100%;
  }
  
  /* Dots */
  .slideshowDots {
    margin-top:-40px;
    height:20px;
    position: absolute;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
  }
  .slideshowDot {
    display: inline-block;
    height: 7px;
    width: 7px;
    border-radius: 50%;
  
    cursor: pointer;
    margin: 0 4px;
  
    background-color: #c4c4c4 ;
  }
  
  .slideshowDot.active {
    background-color:#204095;
    opacity: 0.70;
  }
  
  /* Register Button */
  .register-event{
    position: absolute;
    margin:-80px auto 0 auto;
    left: 50%;
    -webkit-transform: translate(-50%, 0);
            transform: translate(-50%, 0);
    display: none;
  }

