/*Search Bar*/

.Search {
  width: 400px;
  margin: 0px auto;
  position: relative;
  display: flex;
}

.search-box,.close-icon,.search-wrapper {
	position: relative;
	padding: 10px;
}
.search-wrapper {
	width: 500px;
	margin: auto;
}
.SearchSpan {
  width: 10%;
  right:10px;
  padding-right: 3px;
  text-align: center;
  color: #204095;
  font-size: 20px;
  border-radius: 15px 0px 0px 15px ;
}
.search-box {
	width: 80%;
	border: 2px solid #204095; 
  outline: 0;
  border-radius: 15px;
}
.search-box:focus {
	box-shadow: 0 0 3px 3px #203f9593;
  transition: 500ms;
}
.close-icon {
	border:1px solid transparent;
	background-color: transparent;
	display: inline-block;
	vertical-align: middle;
  outline: 0;
  cursor: pointer;
}
.close-icon:after {
	content: "\00D7";
	display: block;
	width: 15px;
	height: 15px;
	position: absolute;
	
	z-index:1;
	right: 35px;
	top: -15px;
	bottom: 0;
	margin: auto;
	padding: 0px 0px 20px 0;
	border-radius: 50%;
	text-align: center;
	color: rgb(119, 118, 118);
	font-weight: normal;
	font-size: 20px;

	cursor: pointer;
}
.search-box:not(:valid) ~ .close-icon {
	display: none;
}
