/*Club CSS*/

.club_head {
  width:100%;
}
.club_head > h1 {
  font-size: 60px;
  font-weight: 600;
  text-align: center;
  padding: 30px;
  margin-bottom: -20px;
  color: #203e91;
  text-align: center;
}
.club_alumni_head h1{
  font-size: 50px;
  text-align: center;
  margin-top: 20px;
}
.club_members{
  padding: 30px 0;
  margin: 20px 2%;
  box-shadow: 0 0 5px 5px rgb(207, 207, 207);
}

.cm_row1, .cm_row2, .cm_row3, .cm_row4, .cm_row5{
  display: flex;
  place-items: center;
  margin-top:20px;
  flex-wrap: wrap;
