}


.alumni{
    display: grid;
    grid-template-columns: 30% 70%;
    width:80%;
    margin:30px 10% 0 10%;
    box-shadow: 0 0 4px 4px #bdbaba;
}
.alumni-heading p{
    font-size: 60px;
    font-weight: 600;
    text-align: center;
    margin-top: 30px;
    color: #203e91;
}
.alumni-image{
    padding-top: 40px;
    margin: 0 auto;
}
.alumni-name{
    font-size:24px;
    color: #90191b;
}
.alumni-year{ font-size: 20px;}
.alumni-role{font-size: 18px; margin: -5px 0 10px 0;}
.alumni-message{
    position: relative;
    
    margin: 30px 20px 0 20px;
    font-size:16px;
}

.alumni-message:before {
    position: absolute;
    font-family: 'FontAwesome';
    margin: -100px 0 0 -28px;
    
    content:"“";
    font-size: 200px;
    color: rgba(0,0,0,0.1);
  }
.alumni-info{
    padding-top: 30px;
    display: flex;
    flex-direction: column;   
}
.alumni-in { font-size: 43px; margin: -4px 10px 0px 0px; color: #203e91}
.alumni-in:hover{ cursor: pointer; color: #58678f;}
.alumni-contact{
    float: right;
    margin: 20px 50px 20px auto;
}
.alumni-contact-btn{
    width: 140px;
    font-size: 20px;
    float: right;
    margin: 0px 50px 20px auto;
    
    padding: 0 5px 0 15px;
    background-color: #90191b;
    border: 2px solid #90191b;
    color: white;
}
.alumni-contact-btn:hover{
    background-color: #965c5d;
    border: 2px solid #965c5d;
    color: white;
}

@media screen and (max-width:900px){
    .alumni{
        display: grid;
        grid-template-columns: 40% 60%;
    }
}
    
@media screen and (max-width:700px){
    .alumni{
        display: grid;
        grid-template-columns:100%;
    }
    .alumni-contact-btn{
        margin: 20px auto;
    }
    .alumni-name, .alumni-year,.alumni-role, .alumni-message{
        text-align: center;
    }
    .alumni-in { margin: -5px 10px 0px 0px; }
    .alumni-contact{
     float: center;
     margin: 20px auto 20px auto;
    }
    .alumni-contact-btn{
     margin: 0px auto 20px auto;
    }
}
